import i18n from "i18next";
import { initReactI18next } from "react-i18next";

// Function to get the language from the URL (without hooks)
const getCurrentLang = () => {
  const params = new URLSearchParams(window.location.search);
  return params.get("language") || "en-US";
};

const currentLang = getCurrentLang();

i18n.use(initReactI18next).init({
  resources: {
    en: {
      translation: {
        movie: "movie",
        series: "Series",
        account: "account",
        logout: "logout",
        register: "register",
        login: "login",
        regbutton: "registration",
        required: "This field is required",
        message: "maximum length 20 characters",
        message1: "Minimum length 4 characters",
        requiredcheckbox: "You must agree with the conditions",
        theme: "theme",
        dark: "dark",
        light: "light",
        green: "green"
      }
    },
    ru: {
      translation: {
        movie: "",
        series: "",
        account: "",
        logout: "",
        register: "",
        login: "",
        regbutton: "",
        required: "    ",
        message: "  20 ",
        message1: "  4 ",
        requiredcheckbox: "    ",
        theme: "",
        dark: "",
        light: "",
        green: ""
      }
    },
    kr: {
      translation: {
        movie: "",
        series: "",
        account: "",
        logout: "",
        register: "",
        login: "",
        regbutton: "",
        required: "  ",
        message: "  20 ",
        message1: "  4 ",
        requiredcheckbox: "      ",
        theme: "",
        dark: "",
        light: "",
        green: ""
      }
    }
  },
  lng: currentLang,
  fallbackLng: currentLang,
  interpolation: {
    escapeValue: false
  }
});

export default i18n;
